* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navMenu ul{
  display: flex;
  flex-direction: row;
}

.navItem {
  padding: 10px;
  margin-right: 15px;
  list-style: none;
}

.navItem a{
  text-decoration: none;
  color: #fff;
}

.btn{
  border: none;
  border-radius: 50%;
  background: red;
  color: #fff;
  width: 30px;
  height: 30px;
  margin: 0 10px;
  cursor: pointer;
  font-size: 22px;
}